.aboutContainer {
	width: 100%;
	height: 100vh;
	background-color: #000000;
	background-image: linear-gradient(315deg, #000 0%, #414141 74%);
	display: flex;
	justify-content: center;
	align-items: center;
}

.aboutContainer .mainContainer {
	/* background-color: blue; */
	width: 600px;
	height: 400px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.aboutContainer .mainContainer a {
	text-decoration: none;
	color: #bbb;
}

.aboutContainer .mainContainer ul {
	list-style-type: none;
}

.aboutContainer .mainContainer img {
	/* margin: auto; */
	width: 200px;
	height: 200px;
	border-radius: 100px;
}

.aboutContainer .mainContainer .main {
	font-size: 30px;
	color: #bbb;
	padding-top: 10px;
}

.aboutContainer .mainContainer .location {
	font-size: 15px;
	font-weight: 400;
}

/* .divider {
	margin: 20px 0;
	width: 200px;
	height: 5px;
	background-color: #bbb;
} */

.aboutContainer .mainContainer .mainContact {
	margin-top: 20px;
}

.aboutContainer .mainContainer .mainContact h3 {
	font-size: 20px;
	text-align: center;
	color: #bbb;
}

.aboutContainer .mainContainer .mainContact div {
	display: flex;
	align-items: center;
	margin-top: 5px;
}
.aboutContainer .mainContainer .mainContact .mainIcon {
	width: 25px;
	height: 25px;
	margin-right: 6px;
}

.aboutContainer .mainContainer .mainLinks {
	display: flex;
}

.aboutContainer .mainContainer .mainLinks li {
	margin: 0 9px;
}

@keyframes slide-from-left {
	0% {
		left: -50%;
		opacity: 0;
	}
	30% {
		opacity: 0;
	}
	100% {
		left: 0;
		opacity: 1;
	}
}
@keyframes slide-from-right {
	0% {
		right: -53%;
		opacity: 0;
	}
	30% {
		opacity: 0;
	}
	100% {
		right: 0;
		opacity: 1;
	}
}

@media (max-width: 360px) {
	.aboutContainer .mainContainer .main {
		font-size: 25px;
	}
	
	.aboutContainer .mainContainer .location {
		font-size: 12px;
	}
}