* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.workContainer {
	background-color: rgba(0, 0, 50, 0.8);
	width: 100%;
	height: 100vh;
	overflow: hidden;
	display: flex;
	align-items: flex-end;
}

.subContainer {
	/* position: relative; */
	width: 1000px;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.imageContainer {
	height: 90vh;
	width: 90vw;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow-x: scroll;
	overflow-y: hidden;
}

.firstImage {
	width: 100%;
	height: auto;
	border-radius: 0 16px 0 16px;
	object-position: top;
}
.leftIcon {
	left: 90px;
}
.rightIcon {
	right: 90px;
}

.chevIcon {
	position: absolute;
	width: 70px;
	height: 70px;
	cursor: pointer;
	top: 50%;
	transform: translateY(-50%);
	z-index: 20;
}

.slide {
	opacity: 0;
	overflow: visible;
}

.slide.active {
	opacity: 1;
	animation: scale 12s;
}

@keyframes scale {
	0% {
		transform: scale(0.7);
		opacity: 0;
	}
	50% {
		transform: scale(0.55);
		opacity: 1;
	}
	75% {
		transform: scale(0.55);
		opacity: 1;
	}
	100% {
		transform: scale(0.7);
		opacity: 0;
	}
}

@media (max-width: 700px) {
	.slide.active {
		opacity: 1;
		animation: scale 12s;
	}
	
	@keyframes scale {
		0% {
			transform: scale(1);
			opacity: 0;
		}
		50% {
			transform: scale(0.7);
			opacity: 1;
		}
		75% {
			transform: scale(0.7);
			opacity: 1;
		}
		100% {
			transform: scale(1);
			opacity: 0;
		}
	}
}

@media (min-height: 758px)  {
	.slide.active {
		opacity: 1;
		animation: scale 12s;
	}
	
	@keyframes scale {
		0% {
			transform: scale(1.5);
			opacity: 0;
		}
		50% {
			transform: scale(1);
			opacity: 1;
		}
		75% {
			transform: scale(1);
			opacity: 1;
		}
		100% {
			transform: scale(1.5);
			opacity: 0;
		}
	}
}