* {
	margin: 0;
}

.container {
	/* background-color: blue; */
	height: 100vh;
	width: 100%;
	font-family: "Times New Roman", Times, serif;
	overflow: hidden;
	display: flex;
	align-items: center;
}

.backgroundImage {
	position: absolute;
	top: 0;
	left: 0;
	object-fit: cover;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	animation: fade-image 3s;
}

@keyframes fade-image {
	from {
		transform: scale(2);
	}
	to {
		transform: scale(1);
	}
}

.overlay {
	position: absolute;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	background-color: rgba(14, 14, 59, 0.5);
}

.text {
	position: absolute;
	height: 55%;
	width: 80%;
	left: 40px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	color: #fff;
}

.text .nameContainer {
	overflow: hidden;
	height: 50px;
}

.text .nameContainer p {
	font-size: 35px;
	font-weight: bold;
	overflow: hidden;
	padding-top: 0px;
	animation: slide-from-bottom 2s;
}

.text .nameContainer p span {
	color: rgb(184, 100, 41);
}

@keyframes slide-from-bottom {
	0% {
		padding-top: 50px;
	}
	100% {
		padding-top: 0px;
	}
}

.text .photodes {
	font-size: 55px;
	line-height: 55px;
	color: rgba(184, 100, 41, 0.9);
}

.text .button {
	display: inline-block;
	color: #fff;
	font-size: 25px;
	padding: 10px 0px;
	border: 1px solid #fff;
	border-radius: 25px;
	width: 180px;
	height: 50px;
	text-align: center;
	animation: fade-in 2s;
}

.text .button:hover {
	border-color: rgba(184, 100, 41, 0.9);
	color: rgba(184, 100, 41, 0.9);
}

@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@media (max-width: 1400px) {
	.text .photodes {
		font-size: 53px;
	}
	.text .button {
		/* padding: 0; */
	}
}

@media (max-width: 1320px) {
	.text .photodes {
		font-size: 50px;
	}
}

@media (max-width: 1250px) {
	.text {
		width: 90%;
	}
	.text .photodes {
		font-size: 45px;
		line-height: 50px;
	}
}

@media (max-width: 1000px) {
	.text {
		height: 50%;
	}
	.text .photodes {
		font-size: 40px;
	}
}

@media (max-width: 890px) {
	.text {
		height: 45%;
		left: 20px;
	}
	.text .nameContainer p {
		font-size: 30px;
	}
	.text .photodes {
		font-size: 35px;
	}
}

@media (max-width: 780px) {
	.text .photodes {
		font-size: 30px;
	}
}

@media (max-width: 670px) {
	.text .photodes {
		font-size: 27px;
	}
	.text .nameContainer {
		overflow: auto;
		height: auto;
	}
}

@media (max-width: 600px) {
	.text .photodes {
		font-size: 25px;
	}
}

@media (max-width: 560px) {
	.text {
		height: 40%;
	}
	.text .nameContainer p {
		font-size: 25px;
	}
	.text .photodes {
		font-size: 23px;
	}
}

@media (min-height: 758px) {
	.text {
		height: 30%;
	}
}
@media (max-width: 320px) {
}
@media screen and (width: 768px) and (height: 1024) {
	.text {
		display: none;
	}
}
