* {
	margin: 0;
}

.nav {
	width: 100%;
	position: absolute;
	/* position: relative; */
	top: 0;
	left: 0;
	z-index: 10;
}

.navContainer {
	max-width: 95%;
	margin: auto;
	height: 60px;
	padding-top: 4px;
	display: flex;
	color: #fff;
}

.logo {
	opacity: 1;
	width: 220px;
	height: 50px;
	object-position: top;
	margin-top: 5px;
}

.ul {
	display: flex;
	font-family: "Times New Roman", Times, serif;
	flex: 1;
	align-items: center;
	justify-content: center;
}

.li {
	padding-right: 50px;
}

.a {
	color: #eee;
	font-size: 27px;
	font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
	font-style: italic;
}

.a:hover {
	color: rgba(184, 100, 41);
}

.menu {
	width: 40px;
	height: 40px;
	margin-top: 9px;
	cursor: pointer;
	display: none;
}

/* Nav Menu */
.navMenu {
	position: absolute;
	top: 0;
	right: -100%;
	height: 100vh;
	width: 50%;
	background-color: rgba(150, 150, 150, 0.9);
	z-index: 20;
	display: none;
}

.navMenu .menuUl {
	width: 50%;
	margin: 30px 0px 0px 30px;
}

.navMenu .menuUl .menuLi {
	padding: 20px 0 3px 0;
	border-bottom: 2px solid #444;
}

.navMenu .menuUl .menuLi .menuA {
	color: #333;
	font-size: 20px;
	font-weight: 600;
}

.navMenu .menuUl .menuLi .menuA:hover {
	color: rgba(184, 100, 41);
}




@media (max-width: 1100px) {
	.logo {
		width: 205px;
		height: 45px;
	}
	.li {
		padding-right: 20px;
	}
	.a {
		font-size: 24px;
	}
}
@media (max-width: 950px) {
	.ul {
		display: none;
	}
	.menu {
		display: block;
		position: relative;
	}
	.navContainer {
		justify-content: space-between;
	}
	.navMenu {
		display: block;
		transition: 0.5s ease-in;
	}
	.navMenu.open {
		right: 0;
	}
	.menuClose {
		position: absolute;
		top: 3px;
		right: 16px;
		z-index: 60;
	}
}
@media (max-width: 400px) {
	.navMenu .menuUl .menuLi .menuA {
		font-size: 17px;
	}
}
